<template>
  <div class="register">
    <section class="form_container">
      <div class="manage_tip">
        <span class="title">杰森在线后台管理系统</span>
        <el-form
          :model="registerUser"
        
          :rules="rules"
          ref="registerForm"
          label-width="80px"
          class="registerForm"
        >
          <el-form-item label="用户名" prop="name">
            <el-input v-model="registerUser.name" placeholder='请输入用户名'></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="registerUser.email" placeholder='请输入邮箱'></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input type='password' v-model="registerUser.password" placeholder='请输入密码'></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="password2">
            <el-input type='password' v-model="registerUser.password2" placeholder='再次输入秘密'></el-input>
          </el-form-item>
          
          <el-form-item label="用户身份">
            <el-select v-model="registerUser.identity" placeholder="请选择你的身份">
      <el-option label="管理员" value="manager"></el-option>
      <el-option label="员工" value="employee"></el-option>
    </el-select>
          </el-form-item>

          <el-form-item>
            <el-button type='primary' class='sumbit_btn' @click="submitForm('registerForm')">注册</el-button>
          </el-form-item>
          <div class="tiparea">
                    <p>已有账号，选择<router-link to='/login'>登陆</router-link></p>
                </div>
        </el-form>
        
      </div>
    </section>
  </div>
</template>

<script>
  export default {
    name:'register',
    data(){
      var validatePass2 = (rule, value, callback) => {
         if (value !== this.registerUser.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return{
        registerUser:{
          name:'',
          email:'',
          password:'',
          password2:'',
          identity:''
        },
        rules:{
          name:[{required:true,message:'用户名不能为空',trigger:'blur'},
          { min: 3, max: 12, message: '长度在 3 到 12 个字符', trigger: 'blur'}],
          email:[{type:'email',required:true,message:'邮箱格式不正确', trigger: 'blur'}],
          password:[{required:true,message:'密码不能为空', trigger: 'blur'},
          { min: 8, max: 20, message: '长度在 8 到 20 个字符', trigger: 'blur'}],
          password2:[{required:true,message:'密码不能为空', trigger: 'blur'},
          { min: 8, max: 20, message: '长度在 8 到 20 个字符', trigger: 'blur'},
          {validator:validatePass2,trigger:'blur'}]
        }
      }
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$axios
            .post('https://www.easy-mock.com/mock/5d6e28552563802ffe217097/register',this.registerUser)
            .then(res=>{
              console.log(this.registerUser)
              this.$message({
                message:'账号注册成功',
                type:'success'
              })
              .catch(err=>{console.log(err)})
            })
          } else {
            console.log('error submit!!');
            return false;
            
          };
          this.$router.push('/login')

        });
      }},
      mounted(){
            localStorage.removeItem('eleToken')
      }
  }
</script>
<style scoped>
.register {
  position: relative;
  width: 100%;
  height: 100%;
  background: url(../assets/bg.jpg) no-repeat center center;
  background-size: 100% 100%;
}
.form_container {
  width: 370px;
  height: 210px;
  position: absolute;
  top: 10%;
  left: 34%;
  padding: 25px;
  border-radius: 5px;
  text-align: center;
}
.form_container .manage_tip .title {
  font-family: "Microsoft YaHei";
  font-weight: bold;
  font-size: 26px;
  color: #fff;
}
.registerForm {
  margin-top: 20px;
  background-color: #fff;
  padding: 20px 40px 20px 20px;
  border-radius: 5px;
  box-shadow: 0px 5px 10px #cccc;
}

.submit_btn {
  width: 100%;
}
.tiparea {
  text-align: right;
  font-size: 12px;
  color: #333;
}
.tiparea p a {
  color: #409eff;
}
</style>